"use client"

import { motion, useScroll, useTransform } from "framer-motion"
import { useRef } from "react"

export default function AboutPage() {
  const containerRef = useRef(null)
  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end end"]
  })

  // Parallax transformations
  const y1 = useTransform(scrollYProgress, [0, 1], [0, -200])
  const y2 = useTransform(scrollYProgress, [0, 1], [0, -100])
  const opacity1 = useTransform(scrollYProgress, [0, 0.3, 0.6], [1, 0.5, 0])
  const scale = useTransform(scrollYProgress, [0, 0.5], [1, 1.2])
  const rotate = useTransform(scrollYProgress, [0, 1], [0, 360])

  const companyData = {
    founded: "2020",
    location: "Hyderabad, Telangana, India",
    founders: ["Kavyanth Munagala"],
    focusAreas: ["AI", "Logistics", "Sustainability", "Education", "Agritech"],
    services: [
      {
        title: "Venture Studio",
        description: "Partners with startups for research, validation, MVP building, roadmap definition, and go-to-market strategy.",
        icon: "üöÄ"
      },
      {
        title: "SaaS Division",
        description: "Builds proprietary software-as-a-service products, starting with Venturemond Cloud Desk.",
        icon: "‚òÅÔ∏è"
      },
      {
        title: "Product Strategy",
        description: "Strategic planning and execution for technology-driven products.",
        icon: "üìä"
      },
      {
        title: "Growth Enablement",
        description: "Comprehensive support to scale tech-enabled ventures.",
        icon: "üìà"
      }
    ]
  }

  return (
    <div ref={containerRef} className="relative bg-white">
      
      {/* Animated Background Elements */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <motion.div
          style={{ rotate }}
          className="absolute top-20 right-10 w-96 h-96 bg-gradient-to-br from-green-200 to-blue-200 rounded-full blur-3xl opacity-20"
        />
        <motion.div
          style={{ y: y1 }}
          className="absolute bottom-20 left-10 w-80 h-80 bg-gradient-to-tr from-green-300 to-cyan-300 rounded-full blur-3xl opacity-15"
        />
      </div>

      {/* Hero Section */}
      <section className="relative min-h-screen flex items-center justify-center pt-24 px-6 overflow-hidden">
        <motion.div
          style={{ scale, opacity: opacity1 }}
          className="text-center z-10"
        >
          <motion.h1
            initial={{ opacity: 0, y: 50 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            className="text-6xl md:text-8xl font-bold text-slate-900 mb-6"
          >
            About <span className="text-green-600">Venturemond</span>
          </motion.h1>
          <motion.p
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            className="text-xl md:text-2xl text-slate-600 max-w-3xl mx-auto leading-relaxed"
          >
            A next-generation venture studio and SaaS company transforming ideas into reality
          </motion.p>
        </motion.div>

        {/* Scroll Indicator */}
        <motion.div
          animate={{ y: [0, 10, 0] }}
          transition={{ duration: 1.5, repeat: Infinity }}
          className="absolute bottom-10 left-1/2 transform -translate-x-1/2"
        >
          <div className="w-6 h-10 border-2 border-green-600 rounded-full flex justify-center pt-2">
            <div className="w-1 h-2 bg-green-600 rounded-full"></div>
          </div>
        </motion.div>
      </section>

      {/* Company Overview Section */}
      <section className="relative py-32 px-6">
        <div className="max-w-6xl mx-auto">
          <motion.div
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            viewport={{ once: true, amount: 0.3 }}
            transition={{ duration: 1 }}
            className="grid md:grid-cols-2 gap-16 items-center"
          >
            <div>
              <motion.h2
                initial={{ x: -50, opacity: 0 }}
                whileInView={{ x: 0, opacity: 1 }}
                viewport={{ once: true }}
                transition={{ duration: 0.8 }}
                className="text-4xl md:text-5xl font-bold text-slate-900 mb-6"
              >
                Who We Are
              </motion.h2>
              <motion.p
                initial={{ x: -50, opacity: 0 }}
                whileInView={{ x: 0, opacity: 1 }}
                viewport={{ once: true }}
                transition={{ duration: 0.8, delay: 0.2 }}
                className="text-lg text-slate-600 leading-relaxed mb-6"
              >
                Venturemond is a next-generation venture studio and SaaS company based in Hyderabad. We partner with founders and businesses to design, build, and scale technology-driven products while developing our own suite of SaaS solutions.
              </motion.p>
              <motion.p
                initial={{ x: -50, opacity: 0 }}
                whileInView={{ x: 0, opacity: 1 }}
                viewport={{ once: true }}
                transition={{ duration: 0.8, delay: 0.4 }}
                className="text-lg text-slate-600 leading-relaxed"
              >
                Our mission is to make startup execution faster, more strategic, and outcome-driven through our full-stack venture studio model and innovative SaaS division.
              </motion.p>
            </div>

            <motion.div
              initial={{ scale: 0.8, opacity: 0 }}
              whileInView={{ scale: 1, opacity: 1 }}
              viewport={{ once: true }}
              transition={{ duration: 0.8 }}
              className="grid grid-cols-2 gap-6"
            >
              {[
                { label: "Founded", value: companyData.founded },
                { label: "Location", value: "Hyderabad, IN" },
                { label: "Focus Areas", value: "5+" },
                { label: "Industries", value: "Multiple" }
              ].map((stat, idx) => (
                <motion.div
                  key={stat.label}
                  initial={{ y: 20, opacity: 0 }}
                  whileInView={{ y: 0, opacity: 1 }}
                  viewport={{ once: true }}
                  transition={{ duration: 0.6, delay: idx * 0.1 }}
                  className="bg-gradient-to-br from-green-50 to-white p-6 rounded-2xl border-2 border-green-100 hover:border-green-600 transition-all duration-300"
                >
                  <div className="text-3xl font-bold text-green-600 mb-2">{stat.value}</div>
                  <div className="text-sm text-slate-600">{stat.label}</div>
                </motion.div>
              ))}
            </motion.div>
          </motion.div>
        </div>
      </section>

      {/* Services Section with Stagger Effect */}
      <section className="relative py-32 px-6 bg-gradient-to-b from-white to-slate-50">
        <div className="max-w-6xl mx-auto">
          <motion.h2
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            className="text-4xl md:text-5xl font-bold text-slate-900 text-center mb-16"
          >
            What We Do
          </motion.h2>

          <div className="grid md:grid-cols-2 gap-8">
            {companyData.services.map((service, idx) => (
              <motion.div
                key={service.title}
                initial={{ opacity: 0, y: 50 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true, amount: 0.3 }}
                transition={{ duration: 0.6, delay: idx * 0.2 }}
                whileHover={{ scale: 1.05, y: -10 }}
                className="bg-white p-8 rounded-3xl shadow-lg border-2 border-slate-100 hover:border-green-600 transition-all duration-300"
              >
                <div className="text-5xl mb-4">{service.icon}</div>
                <h3 className="text-2xl font-bold text-slate-900 mb-3">{service.title}</h3>
                <p className="text-slate-600 leading-relaxed">{service.description}</p>
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* Focus Areas with Reveal Animation */}
      <section className="relative py-32 px-6">
        <div className="max-w-6xl mx-auto">
          <motion.h2
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            viewport={{ once: true }}
            className="text-4xl md:text-5xl font-bold text-slate-900 text-center mb-16"
          >
            Our Focus Areas
          </motion.h2>

          <div className="flex flex-wrap justify-center gap-4">
            {companyData.focusAreas.map((area, idx) => (
              <motion.div
                key={area}
                initial={{ opacity: 0, scale: 0 }}
                whileInView={{ opacity: 1, scale: 1 }}
                viewport={{ once: true }}
                transition={{ 
                  duration: 0.5, 
                  delay: idx * 0.1,
                  type: "spring",
                  stiffness: 200
                }}
                whileHover={{ scale: 1.1, rotate: 5 }}
                className="px-8 py-4 bg-gradient-to-r from-green-600 to-green-700 text-white font-semibold rounded-full text-lg shadow-lg hover:shadow-2xl transition-all duration-300 cursor-pointer"
              >
                {area}
              </motion.div>
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section with Parallax */}
      <motion.section
        style={{ y: y2 }}
        className="relative py-32 px-6 bg-gradient-to-br from-green-600 to-green-800"
      >
        <div className="max-w-4xl mx-auto text-center">
          <motion.h2
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            className="text-4xl md:text-5xl font-bold text-white mb-6"
          >
            Ready to Build the Future?
          </motion.h2>
          <motion.p
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ delay: 0.2 }}
            className="text-xl text-green-50 mb-10"
          >
            Partner with us to turn your vision into reality
          </motion.p>
          <motion.a
            href="/#contact"
            initial={{ opacity: 0, scale: 0.8 }}
            whileInView={{ opacity: 1, scale: 1 }}
            viewport={{ once: true }}
            transition={{ delay: 0.4 }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="inline-block px-10 py-5 bg-white text-green-600 font-bold rounded-xl text-lg shadow-2xl hover:shadow-3xl transition-all duration-300"
          >
            Get in Touch
          </motion.a>
        </div>
      </motion.section>

    </div>
  )
}
